/** 座位朝向 与服务器座位对应 1南2北 */
export enum ErmjLocation{
    South = 1,
    North
}

export enum ErmjMjActState{
    Disable = 0,
    Deal,
    Change,
    Play,
}

export default class ErmjGameConst{
    public static readonly Gid = 2101;         // 2101
    public static readonly mahjongTotal = 72;
    public static readonly mahjongWall = 2;
    /** 牌桌最大支持人数 */
    public static readonly maxPlayerCount = 2;
    /** 本地座位映射到桌面 */ 
    public static readonly localToViewMap = {
        
    };
    public static readonly LevelSpCfg = {
        "l0": "changci_01",
        "l1": "changci_02",
        "l2": "changci_03"
    }
    /** 玩家操作提示图片帧 */
    public static readonly StateSpStrCfg = {
        Chow: "chi",
        Pong: "peng",
        Win: "dianpao",
        Kong: "gang",
        WinAll: "zimo",
        Ting: "ting",
        RobWin: "qianggang",
    }
    /** 座位提示栏配置 根据自己所处方位设定 */
    public static readonly askNoticeStrCfg = {
        [ErmjLocation.South]: ["daojishi", "xuanzhong_hong", "xuanzhong_lan"],
        [ErmjLocation.North]: ["daojishi_01", "xuanzhong_lan_01", "xuanzhong_hong_01" ]
    }

    public static playMusic(path: string, isAutoRealse: boolean){
        Global.Audio.playBundleMusic(ErmjGameConst.Gid, path);
    }

    public static playSound(path: string, isAutoRealse: boolean){
        Global.Audio.playBundleSound(ErmjGameConst.Gid, path);
    }

    /**
    * 给节点添加点击事件
    * @param root 根节点
    * @param path 相对于根节点的路径
    * @param callback 回调函数
    * @param target 回调函数的调用者this
    * @param transition 按钮点击过渡类型
    * @param time 过渡时间
    */
   public static addCommonClick(root: cc.Node, path: string, callback: Function, target?: any, transition = cc.Button.Transition.SCALE, playSound: boolean = false): cc.Node {
       return Global.UIHelper.addCommonClick(root, path, callback, target, transition, null, playSound);
   }
}